//////////////////////////////
// CORE / GENERIC / ATTRIBUTES
//////////////////////////////


// Add scroll margin to all elements which can be targeted
// https://piccalil.li/quick-tip/add-scroll-margin-to-all-elements-which-can-be-targeted
[id] {
  scroll-margin-top: #{ms(8)}rem; // Account for height of tabs when switching tab panel IDs
}


*:not([role="dialog"])[hidden] {
  display: none !important;
}


// Do not set dialogs to `display: none` by default
// https://developer.paciellogroup.com/blog/2018/06/the-current-state-of-modal-dialog-accessibility/
[role="dialog"][hidden] {
  display: block !important;
  visibility: hidden !important;
}
